<template>
  <div>
    <van-nav-bar
      class="app_title"
      title="公告详情"
      left-text="返回"
      left-arrow
      @click-left="$toPage(-1)"
    />
    <div style="height: 50px;"></div>
    <div v-if="newsList.length>0" class="news_box">
      <div>
        <h1 class="news_title">{{newsList[newsIndex].type}} {{newsList[newsIndex].title}}</h1>
      </div>
      <p class="news_time"> 
        发布时间：{{newsList[newsIndex].date}} {{newsList[newsIndex].time}}  
        <span>作者：</span>
        <span>{{newsList[newsIndex].editor}}</span> 
      </p>
      <div class="news_details">
        <img :src="newsList[newsIndex].img" alt class="notice-pic">
      </div>
      <van-pagination
        class="page_box"
        v-model="pageNum" 
        :page-count="newsList.length"
        mode="simple" 
        prev-text="上一条"
        next-text="下一条"
      />
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        newsList:{},
        newsIndex:0,
      }
    },
    mounted(){
      this.newsIndex = this.$route.query.index;
      this.newsList = JSON.parse(sessionStorage.getItem("newsListAll"));
    },
    methods:{

    },
    computed:{
      pageNum:{
        get: function () {
          return parseInt(this.newsIndex) +1;
        },
        set: function (v) {
          this.newsIndex = v-1;
        }
      }
    }
    
  }
</script>

<style scoped>
  .app_title{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }
  .news_box{
    padding: 20px;
  }
  .news_title{
    font-size: 36px;
  }
  .news_time{
    font-size: 24px;
    color: #999;
  }
  .news_details{
    margin-bottom: 80px;
  }
  .news_details img{
    width: 100%;
    display: block;
  }
  .page_box{
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #FFFFFF;
    right: 0;
  }
</style>
